@use 'variables/index' as *;

.wp-block-acf-cta-1-3,
.wp-block-acf-cta-2-3,
.wp-block-acf-revista {
  color: var(--color-black);
  content: 'CTA';
  min-height: 100px;
  background-color: var(--color-white);
  border: solid 4px var(--color-black);
  float: left;
  box-sizing: border-box;
}

.wp-block-columns {
  gap: 20px;
}

